.navigation {
  height: 64px;
  background: #464545;
  padding: 13px 25px;
  display: flex;
  position: fixed;
  width: 100%;
  justify-content: space-between;
  z-index: 100; }
  .navigation.expand .navigation__logo {
    filter: blur(5px); }
  .navigation__menu {
    line-height: 38px;
    height: 38px;
    text-align: right;
    margin-right: 30px; }
    .navigation__menu .menu__item {
      font-size: 20px;
      color: white;
      padding: 0 20px; }
      .navigation__menu .menu__item:focus {
        text-decoration: none; }
      .navigation__menu .menu__item.active {
        color: #0090FF; }
  .navigation__logo {
    width: 130px;
    height: 25px;
    line-height: 38px; }
    .navigation__logo .logo {
      width: 100%;
      height: 100%; }
  .navigation__right-content {
    display: flex;
    color: #fff;
    height: 40px;
    width: 200px;
    justify-content: flex-end; }
  .navigation .navigation__login {
    cursor: pointer; }
  .navigation__switch-lang {
