    .navigation__switch-lang .active {
      font-weight: 700;
      opacity: 1; }
  .navigation__hamburger {
    width: 2.60417vw;
    cursor: pointer;
    fill: #fff; }
    .navigation__hamburger .svg-icon {
      width: 100%;
      height: 100%; }
  @media screen and (max-width: 450px) {
    .navigation {
      width: 100vw;
      height: 9.23645vh;
      padding: 3.07882vh 1.33333vw 3.07882vh 6.66667vw; }
      .navigation__logo {
        width: 34.66667vw;
        height: 3.07882vh; }
        .navigation__logo .logo {
          width: 100%;
          height: 100%; }
      .navigation__hamburger {
        width: 10.66667vw;
        height: 4.92611vh;
        margin-top: -1.23153vh; }
      .navigation__right-content {
        height: 3.07882vh;
        width: 21.33333vw;
        justify-content: flex-end; }
      .navigation__switch-lang {
        display: none; } }
